@font-face {
	font-family: "game-font";
	src: url(../asset/fonts/DungGeunMo.ttf);
}
.homeCont {
    height: 90vh;
    width:100%;
    font-size: 20pt;
    display: flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;

}

.mainImg{
    height: 350px;
    width: 350px;
    margin-bottom:80px;

}

.homeBtn{
    margin-top:60px;
    font-size: 20pt;
    display: flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
	font-family: "game-font";

    float: left;
    width: 120px;
    padding: 0;
    margin: 10px 20px 10px 0;
    font-weight: 600;
    text-align: center;
    line-height: 50px;
    color: #FFF;
    border-radius: 5px;
    transition: all 0.2s ;
    background: #888888;
}

.logoDiv{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.progressBar{
    
    margin-top: 40px;
}

.selectBtn{
    margin: 10px auto;
    margin-left: 15px;
    margin-right: 15px;
    font-size: 15pt;
    
    font-family:"game-font";
    display: flex;
    justify-content:center;
    align-items:center;
    border: 2px solid #8F8F8F;
    background-color : #D9D9D9;
    padding:10px;
    padding-left:20px;
    padding-right:20px;
    color: black;
    

}
.topBar{
    background-color : #000080;
    color: white;
    padding:2px;
	font-family: "game-font";
}

.gamePage{
    background-color : #008080;
    height: 100vh;
}
.resultImgA{
    margin-top: 100px;
}
.fakeImg{
    width: 100%;
}